import React from 'react';
import styles from './Box3d.module.css';

let arr = [1,2,3,4];

function Box() {
    return (
        <div className={styles.box}>
            <div className={styles.shape}>
                {arr.map((i)=><div className={styles.side}/>)}
            </div>
        </div>
    );
}

export default Box;
