import React from 'react';
import styles from './Circle2.module.css';

let arr = [1, 1, 1, 1, 1, 1, 1, 1, 1];

function App() {
    return (
        <div className={styles.container}>
            <div className={styles.circle}>
                {arr.map((e, i) =>
                    <span key={i}
                          className={styles.side}
                          style={{'--i': i + 1}}/>)}
            </div>
        </div>
    );
}

export default App;
