import React from 'react';
import './App.css';
import ProgressBar from './components/ProgressBar/ProgressBar';
import styled from "styled-components";
import Loader from './components/Loader/Loader';
import NeonButton2 from './components/NeonButton/NeonButton2';
import Parallax from './components/Parallax/Parallax';
import Circle from './components/Circle/Circle';
import Circle2 from './components/Circle/Circle2';
import Draggble from './components/Draggbale/Draggble';
import Box3d from  './components/Box3d/Box3d';

let Container = styled.div`
    display: flex;
    align-self: center;
    margin: 0;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    ${props => 
        (
            (props.Y ? `height: ${props.Y} ;` : '') +
            (props.X ? `width: ${props.X} ;` : '') +
            (props.bg ? `background: ${props.bg} ;` : '')+
            (props.br ? `border-radius: ${props.br} ;` : '') +
            (props.fd ? `flex-direction: ${props.fd} ;` : '')  
        ) 
    }
`;


function App() {
    return (
        <div className="App">
            <header className="App-header">
                <Container X={'100vw'} Y={'calc(100vh)'}>
                <Container X={'550px'}
                           Y={'550px'}
                           bg={'radial-gradient(rgb(120,213,255),rgba(0,129,232,0))'}
                           br={'50%'}>
                    <Loader/>
                </Container>
                    <h1>Scroll to see more</h1>
                </Container>
                <Container X={'100vw'}
                           Y={'calc(100vh)'}
                           bg={'#050801'}
                           fd={'row'}
                >
                    <NeonButton2 />
                    <NeonButton2 filter={'290deg'}/>
                    <NeonButton2 filter={'110deg'}/>
                </Container>
                <Draggble/>
                <Circle2/>
                <Circle/>
                <Parallax/>
                <Box3d/>
                <ProgressBar/>
            </header>
        </div>
    );
}

export default App;
